import React from 'react'
import Layout from '../components/Layout'
import { graphql, useStaticQuery } from 'gatsby'
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { BgImage } from "gbimage-bridge"

export default function Oprichters() {
    const { placeholderImage, placeholderAnu, placeholderMartien } = useStaticQuery(
        graphql`
          query {
            placeholderImage: file(relativePath: {eq: "Vlinder.jpg"}) {
              childImageSharp {
                gatsbyImageData(
                  width: 1600
                  quality: 80
                  webpOptions: {quality: 90}
                  formats: [AUTO, WEBP, AVIF]
                )
              }
            }
            placeholderAnu: file(relativePath: {eq: "AnuProfielfoto2.jpeg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 600
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderMartien: file(relativePath: {eq: "martien_van_der_berg.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 600
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
    
          }
          
        `
      )
      const pluginImage = getImage(placeholderImage);
      const AnuImage = getImage(placeholderAnu);
      const MartienImage = getImage(placeholderMartien);
    
    return (
        <Layout>
             <BgImage image={pluginImage} className="bg"
      Tag="section">
          <div className="foundersHeading">
          <h1 className="foundersHeading">OPRICHTERS</h1>
          </div>
          </BgImage>
        <div className="genText row">
            <div className="col-sm-4">
          <div className="leftImage">
          <GatsbyImage image={AnuImage} alt={"foto oprichter Anu de Rooden"}/>
          </div>
          </div>
          <div className="col-sm-8">
          <h3 className="">Anu de Rooden</h3>

          <p>
          Mijn leven is een lange aaneenschakeling van avonturen geweest: op mijn 16de 
          begon ik mijn grenzen te verleggen en dat ben ik tot nu toe blijven doen. 
          Een van de belangrijke momenten in mijn leven was in 2013, toen ik van 
          India naar Nepal reisde, waar mijn lange spirituele zoektocht onverwachts 
          tot een einde kwam. Terugkijkend begrijp ik dat het onvoorspelbare en 
          grillige verloop van mijn leven een voorbereiding was voor wat er komen 
          ging. De herkenning van een spirituele leraar is niet te omschrijven. 
          Het markeerde het begin van een nieuwe manier van leven, met toewijding, 
          overgave, acceptatie en vertrouwen.
          </p>
          <p>
          Begin 2020 kwam mijn nomadenbestaan door corona in Nederland tot stilstand. 
          Het was als een uitnodiging om het roer om te gooien. Het is een nieuwe 
          ervaring om op één plek te blijven, mijn rugzak op te bergen en iets te 
          laten ontstaan. Nu deel ik mijn ervaringen en kennis over voeding, ayurveda, 
          yoga en klankschalen met een ieder die geïnteresseerd is of het nodig heeft. 
          Het is een geschenk om in alle rust en met toewijding te kunnen delen en de 
          eerste positieve resultaten te mogen ervaren, waaronder de geboorte van Aikyam.
          </p>
          </div>
          <div className="col-sm-4">
          <div className="leftImage">
          <GatsbyImage image={MartienImage} alt={"foto oprichter Martien van den Berg"}/>
         </div>
         </div>
          <div className="col-sm-8">
         <h3 className="">Martien van den Berg</h3>
          <p>
          Al op jonge leeftijd stelde ik mezelf en anderen allerlei vragen over het bestaan.
        In mijn puberteit werd mijn zoektocht naar zingeving en spiritualiteit intensiever. 
        Ik bezocht vele kerkdiensten vanuit verschillende tradities in binnen- en buitenland 
        en verdiepte me daarnaast in Oosterse stromingen zoals de macrobiotiek. Door de macrobiotiek 
        kreeg ik ook belangstelling voor antroposofie, ayurveda en yoga. 
          </p>
          <p>
          Als jonge rebel ging ik op 16 jarige leeftijd op mezelf wonen om mezelf steeds beter 
          te leren kennen. Het was de eerste jaren geen gemakkelijke periode. Ik was vooral aan 
          het vechten met mezelf en tegen onrechtvaardigheid vanuit mijn perspectief, botste 
          tegen vele muren. Tegelijkertijd was er de zoektocht naar een natuurlijk, simpel bestaan 
          en een andere wijze van samenleven in harmonie met alles en iedereen. Hierdoor ging ik me 
          meer verdiepen in het sjamanisme, ook weer verschillende stromingen en tradities. 
          </p>
          <p>
          Een aantal jaren geleden kwam ik in contact met Guru Premangee uit Nepal en ben sindsdien 
          verbonden met de organisatie die hij heeft opgericht. Zijn levenslessen resoneren diep in mij. 
          De eenvoud, maar tegelijkertijd diepte, van de aanwijzingen voelen als thuiskomen. 
          Verbonden met de aarde, met de elementen en alle levensvormen. Waar de aandacht uitgaat naar 
          het positieve stromende en het grote vertrouwen, waar iedereen er kan zijn ongeacht achtergrond 
          of levensovertuiging. Heel graag werk ik dan ook via Aikyam mee om dit gedachtegoed ook in 
          Nederland te laten wortelen.
          </p>
        </div>
        </div>
        </Layout>
    )
}
